export const homePage = {
  data() {
    return {
      successPath:1
    };
  },
  methods: {
    // 
    toPersonalPageFn() {
      this.$router.push("/personal");
    },
    // 
    toPwdPageFn() {
      this.$router.push("/password");
    },
    // 
    toRegPageFn() {
      this.$router.push("/register");
    },
    // 
    toRegisterSuccessPageFn(){
      this.$router.push({
        path:"/register-success",
        query: {
          id: this.successPath
        }
      });
    },
    // 
    toHomePageFn() {
      this.$router.push("/");
    },
    // 
    toLoginPageFn() {
      this.$router.push("/login");
    }
  }
};
